import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { isFunction } from 'lodash';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@devsnc/sn-record-control-wrapper';
import '@servicenow/now-checkbox';
import { adaptCallbackForLegacyAPI } from '@devsnc/sn-controls-common';

import {
	isAttrTrue,
	accessibilityAttributes
} from '@devsnc/sn-controls-common';

import style from './checkbox.scss';

/**
 * Simple checkbox component. Value changes will always use string values:
 * `"true"` or `"false"`
 */
const view = (state, { dispatch }) => {
	const { properties } = state;
	const {
		description,
		messages,
		name,
		label,
		readonly,
		invalid,
		required,
		value
	} = properties;
	const ariaProperties = {
		...properties.configAria,
		...accessibilityAttributes(properties)
	};
	return (
		<sn-record-control-wrapper messages={messages} description={description}>
			<now-checkbox
				name={name}
				label={label}
				readonly={isAttrTrue(readonly)}
				invalid={isAttrTrue(invalid)}
				required={isAttrTrue(required)}
				configAria={ariaProperties}
				manageChecked
				checked={isAttrTrue(value)}
			/>
		</sn-record-control-wrapper>
	);
};

/**
 *
 *  Simple checkbox component. Value changes will always use string values:
 * `"true"` or `"false"`
 *
 * This componenet is meant to be consumed by forms and simply converts properties that `now-checkbox` can understand.
 * If you want to use a checkbox that is not on a form directly, we recommend that you use `now-checkbox` directly
 *
 * ```jsx
 * <now-record-checkbox name="checkbox" label="I am a checkbox!" value="true" />
 * ```
 *
 * @seismicElement now-record-checkbox
 * @summary Simple checkbox component. Value changes will always use string values: `"true"` or `"false"`
 *
 * */
createCustomElement('now-record-checkbox', {
	type: snabbdom,
	view,
	styles: style,
	properties: {
		/**
		 * aria-describedby
		 *
		 * Accessible Attribute to indicate the IDs of the elements that describe the object.
		 * Which element (by ID) provides more description for the component
		 * @type {string}
		 * @private
		 */
		ariaDescribedby: {},
		/**
		 * aria-label
		 *
		 * Accessible Attribute to define a string that labels the current element.
		 * Accessible label for the component. Only needed when the `label` prop is not
		 * descriptive enough on its own.
		 * @type {string}
		 * @private
		 */
		ariaLabel: {},
		/**
		 * aria-labelledby
		 *
		 * Accessible Attribute establishes relationships between objects and their label(s)
		 * Which element (by ID) acts as a label for the component
		 * @type {string}
		 * @private
		 */
		ariaLabelledby: {},
		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},
		/**
		 * An object whose items are all aria properties in the format 'aria-*' : 'value'
		 * @type {{'aria-*': string}}
		 */
		configAria: {},
		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		invalid: { default: false },
		/**
		 * It's a label! All form controls need a label!
		 * @type {string}
		 */
		label: {},
		/**
		 * Array of message objects that must contain type and message itself.
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: { default: [] },
		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: {},
		/**
		 * onBlur event callback
		 * @type {function}
		 * @private
		 */
		onValueChange: {},
		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 */
		readonly: { default: false },
		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {boolean}
		 */
		required: { default: false },
		/**
		 * Underlying data for the component.
		 * @type {string}
		 */
		value: {}
	},
	dispatches: {
		/**
		 * Dispatched when checkbox value is changed (true to false and vice versa).
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_CHECKBOX#VALUE_CHANGED': {}
	},
	actionHandlers: {
		['NOW_CHECKBOX#CHECKED_SET']: {
			effect: ({ state, action, dispatch }) => {
				const { name, value } = action.payload;
				const { onValueChange } = state.properties;
				if (isFunction(onValueChange)) {
					adaptCallbackForLegacyAPI(onValueChange)({
						name,
						value: value.toString()
					});
				}
				dispatch('NOW_RECORD_CHECKBOX#VALUE_CHANGED', {
					name,
					value: value.toString()
				});
			},
			stopPropagation: true
		}
	}
});
